ns/Follow.vue'\nconst Follow = () => import('../components/buttons/Follow.vue')\n\nexport default {\n  name: 'UserBanner',\n  // props: ['user'],\n  data () {\n    return {}\n  },\n  components: {\n    Follow,\n  },\n  computed: {\n    ...mapGetters(['usersFilter', 'currentUser']),\n    user () {\n      return this.usersFilter(this.$route.params.id)\n    },\n    followers () {\n      return this.user.followers.count\n    }\n  },\n  methods: {\n    userSettings () {\n      this.$router.push({ name: 'UserSettings' })\n    }\n  },\n}\n</script>\n"],"sourceRoot":""}]);
